import { AlertController, ToastController } from '@ionic/angular';
import { Component, ViewChild, OnInit } from "@angular/core";
import { Router, ActivatedRoute } from "@angular/router";
import { ProductService } from "../../providers/product.services";
import { async } from '@angular/core/testing';
import { LoginService } from '../../providers/login.services';

@Component({
  selector: "page-showInterest",
  templateUrl: "showInterest.html",
  styleUrls: ["./showInterest.scss"],
})
export class ShowInterestPage implements OnInit {
  public userData = {
    name: "",
    mobile: "",
    city: "",
    title: "From Application",
    description: "",
    ownerId: "",
    service:""
  };
  constructor(
    private _ProductService: ProductService,
    private route: ActivatedRoute,
    private Router:Router,
    private toastController:ToastController,
    private alertController:AlertController,
    private _LoginService: LoginService,
  ) {}

  ngOnInit() {
    this.route.params.subscribe((params) => {
      this.userData.description = params.car;
      this.userData.service = params.service;
    });
  }

  async submit() {
console.log(this.userData);

let len = this.userData.mobile.toString().length;
    if(len !== 10){
      const toast = await this.toastController.create({
        message: "Invalid Mobile Number",
        duration: 3000,
        color:'warning',
        position: "bottom",
        animated: true,
      });
      toast.present();
      return
    }

    this.sendOTP();
    this.presentAlertPrompt();


   
  }

  async presentAlertPrompt() {
    const alert = await this.alertController.create({
      cssClass: 'my-custom-class',
      header: 'Verify Number',
      inputs: [
        {
          name: 'otp',
          type: 'number',
          placeholder: 'Enter OTP'
        },
      ],
      buttons: [
        {
          text: 'Cancel',
          role: 'cancel',
          cssClass: 'secondary',
          handler: () => {
            console.log('Confirm Cancel');
          }
        }, {
          text: 'Submit',
          handler:async (data) => {
            console.log('Confirm Ok',data);
            if(data.otp.toString().length !== 4){
              const toast = await this.toastController.create({
                message: "Only 4 Digit is allowed",
                duration: 3000,
                color:'warning',
                position: "bottom",
                animated: true,
              });
              toast.present();
              return
            }else{
              this.verify(data.otp)
            }

          }
        }
      ]
    });


    await alert.present();
  }

  async sendOTP(){
      let toast = await this.toastController.create({
        message: "OTP sent..!!",
        duration: 2000,
        color: "success",
        position: "top",
        animated: true,
      });
  
        this._LoginService
          .sendOTP({
            mobile: this.userData.mobile,
          })
          .subscribe(async(result) => {
            console.log("send OTP result", result);
          });
      }
  

  verify(otp) {
    let payload = {
      mobile: this.userData.mobile,
      otp: otp,
      ownerId: this._LoginService.getOwnerId(),
      isSocialLogin: false,
    };
    this.verifyOTP(payload);
  }

  verifyOTP(payload) {
    this._LoginService.verifyOTP(payload).subscribe(
     async (result) => {

        if(result.hasOwnProperty('otpDetails')){
          let toast = await this.toastController.create({
            message: "Wrong OTP",
            duration: 3000,
            color: "warning",
            position: "top",
            animated: true,
          });
          toast.present();
          
          return
        }
        // this.login.mobile = null;
        console.log("verify OTP result", result);
        console.log("_id", result.customerDetails._id);
        localStorage.setItem("userId", result.customerDetails._id);
        // localStorage.setItem("userId", "5ea4259790a47f96283ee75d");
        localStorage.setItem("AuthToken", result.authToken);

        if (
          this.userData.name &&
          this.userData.city &&
          this.userData.mobile &&
          this.userData.title
        ) {
          this._ProductService.saveQuery(this.userData).subscribe(async(data: any) => {
            this.userData = {
              name: "",
              mobile: "",
              city: "",
              title: "",
              description: "",
              ownerId: "",
              service:''
            };
    
            const toast = await this.toastController.create({
              message: "Thanks You for your interest",
              duration: 3000,
              color:'success',
              position: "bottom",
              animated: true,
            });
            toast.present();
            
            setTimeout(()=>{
              this.Router.navigateByUrl('/home');
            },2000)
          });
        }else{
          let toast = await this.toastController.create({
            message: "Please Enter All Fields",
            duration: 3000,
            color: "warning",
            position: "bottom",
            animated: true,
          });
          toast.present();
        }

        
      },
      async (err) => {
        console.log(err);
   
        let toast = await this.toastController.create({
          message: "Invalid Data",
          duration: 3000,
          color: "secondary",
          position: "bottom",
          animated: true,
        });
        toast.present();
      }
    );
  }
}
