import { Injectable } from "@angular/core";
import { HttpClient, HttpHeaders } from "@angular/common/http";
import { map } from "rxjs/operators";
import { environment } from "../../environments/environment";

@Injectable({
    providedIn: "root"
})
export class LoginService {
    constructor(private http: HttpClient) { }

    public baseUrl: String = environment.baseUrl;
    public ownerId = "5f6f0e8c1f46bf3f11660245"

    sendOTP(payload) {
        let response;
        return this.http.post(this.baseUrl+"/api/v1/customer/SendOTP", payload).pipe(
            map((data) => {
                response = data
                return response.object
            })
        );
    }


    getOwnerId() {
        return this.ownerId
    }


    verifyOTP(payload) {
        let response;
        return this.http.post(this.baseUrl+"/api/v1/customer/VerifyOTP", payload).pipe(
            map((data) => {
                response = data
                console.log(response, "response")
                return response.object
            })
        );
    }

    


    socialMediaLogin(payload){
      let response;
      return this.http.post(this.baseUrl+"/api/v1/customer/Register", payload).pipe(
          map((data) => {
              response = data
              console.log(response, "response")
              return response.object
          })
      );
    }



}
